import React, { useState } from 'react';
import "../recommend/FilterButtons.css";

// 
import { useTranslation } from "react-i18next";



//    -  
const FilterButtons = ({ selectedFilters, setSelectedFilters }) => {

  const { t } = useTranslation();

  //     
  const handleButtonClick = (filter) => {
    //    
    const index = selectedFilters.indexOf(filter);

    if (index === -1) {
      //    ,    
      setSelectedFilters([...selectedFilters, filter]);
    } else {
      //    ,    
      setSelectedFilters(selectedFilters.filter((item) => item !== filter));
    }
  };

  return (
    <div className="filters">
      <button
        className={selectedFilters.includes("") ? "selected_filter_button_street" : "filter_button"}
        onClick={() => handleButtonClick("")}
      >
       <div className='font'> {t('kind.k1')}</div> 
      </button>
      <button
        className={selectedFilters.includes("") ? "selected_filter_button_place" : "filter_button"}
        onClick={() => handleButtonClick("")}
      >
        <div className='font'> {t('kind.k2')}</div> 
      </button>
      <button
        className={selectedFilters.includes("") ? "selected_filter_button_culture" : "filter_button"}
        onClick={() => handleButtonClick("")}
      >
                <div className='font'> {t('kind.k3')}</div> 
      </button>
      <button
        className={selectedFilters.includes("") ? "selected_filter_button_shopping" : "filter_button"}
        onClick={() => handleButtonClick("")}
      >
                <div className='font'> {t('kind.k4')}</div> 
      </button>
      <button
        className={selectedFilters.includes("") ? "selected_filter_button_nature" : "filter_button"}
        onClick={() => handleButtonClick("")}
      >
                <div className='font'> {t('kind.k5')}</div> 
      </button>
      <button
        className={selectedFilters.includes("") ? "selected_filter_button_food" : "filter_button"}
        onClick={() => handleButtonClick("")}
      >
      <div className='font'> {t('kind.k6')}</div> 
      </button>
      <button
        className={selectedFilters.includes("") ? "selected_filter_button_foreign" : "filter_button"}
        onClick={() => handleButtonClick("")}
      >
        <div className='font'>{t('kind.k7')}</div> 
      </button>
    </div>
  );
};

export default FilterButtons;