import React, { useState } from 'react';
import "../recommend/FilterButtons.css";
// 
import { useTranslation } from "react-i18next";


//    -  
const FilterButtons = ({ selectedFilters, setSelectedFilters }) => {
  const { t } = useTranslation();


  //     
  const handleButtonClick = (filter) => {
    //    
    const index = selectedFilters.indexOf(filter);

    if (index === -1) {
      //    ,    
      setSelectedFilters([...selectedFilters, filter]);
    } else {
      //    ,    
      setSelectedFilters(selectedFilters.filter((item) => item !== filter));
    }
  };

  return (
    <div className="filters">
      <button
        className={selectedFilters.includes("") ? "selected_filter_button_foreign" : "filter_button"}
        onClick={() => handleButtonClick("")}
      >
        {t("kind.k8")}
      </button>
      <button
        className={selectedFilters.includes("") ? "selected_filter_button_shopping" : "filter_button"}
        onClick={() => handleButtonClick("")}
      >
        {t("kind.k9")}
      </button>
      <button
        className={selectedFilters.includes("") ? "selected_filter_button_culture" : "filter_button"}
        onClick={() => handleButtonClick("")}
      >
        {t("kind.k10")}
      </button>
      <button
        className={selectedFilters.includes("") ? "selected_filter_button_nature" : "filter_button"}
        onClick={() => handleButtonClick("")}
      >
         {t("kind.k11")}
      </button>

    
    </div>
  );
};

export default FilterButtons;
