const containerStyle = {
  width:'100%',
  height: '100%',
  minHeight:"500px"
};

const defaultCenter = {
  lat: 37.5665,
  lng: 126.9780
};

const markerIcons = {
  "": "http://maps.google.com/mapfiles/ms/icons/green-dot.png",
  "": "http://maps.google.com/mapfiles/ms/icons/blue-dot.png",
  "": "http://maps.google.com/mapfiles/ms/icons/red-dot.png",
  "": "http://maps.google.com/mapfiles/ms/icons/yellow-dot.png"
};

const Map = ({ placeName, inf1, inf2, inf3, inf4, selectedFilters }) => {
  const [currentPosition, setCurrentPosition] = useState(defaultCenter);
  const [selectedMarker, setSelectedMarker] = useState(null);
  const [markers, setMarkers] = useState([]);
  const [isDataLoaded, setIsDataLoaded] = useState(false); //      
  const [placeNameData, setPlaceNameData ] = useState([]);
  const isPlaceInTouristSpots = touristSpots.some(spot => spot.name === placeName);
  const integrateData = (inf1, inf2, inf3, inf4, selectedFilters) => {
    const filteredMarkers = [];
  
    //     
    //   
    if (selectedFilters.includes("")) {
      inf1.forEach((event) => {
        const { EVENT_X, EVENT_Y, EVENT_NM, EVENT_PLACE, EVENT_PERIOD, URL, THUMBNAIL } = event;
        filteredMarkers.push({
          lat: parseFloat(EVENT_Y),
          lng: parseFloat(EVENT_X),
          type: "",
          detail: {
            name: EVENT_NM,
            place: EVENT_PLACE,
            period: EVENT_PERIOD,
            url: URL,
            thumbnail: THUMBNAIL
          }
        });
      });
    }
  
    //   
    if (selectedFilters.includes("")) {
      inf2.forEach((parking) => {
        const { LAT, LNG, PRK_NM, ADDRESS, RATES, PAY_YN } = parking;
        filteredMarkers.push({
          lat: parseFloat(LAT),
          lng: parseFloat(LNG),
          type: "",
          detail: {
            name: PRK_NM,
            address: ADDRESS,
            rates: RATES,
            pay: PAY_YN
          }
        });
      });
    }
  
    //   
    if (selectedFilters.includes("")) {
      inf3.forEach((subway) => {
        const { SUB_STN_X, SUB_STN_Y, SUB_STN_NM, SUB_STN_LINE, SUB_STN_RADDR, SUB_STN_JIBUN } = subway;
        filteredMarkers.push({
          lat: parseFloat(SUB_STN_Y),
          lng: parseFloat(SUB_STN_X),
          type: "",
          detail: {
            name: SUB_STN_NM,
            line: SUB_STN_LINE,
            address: SUB_STN_RADDR,
            jibun: SUB_STN_JIBUN
          }
        });
      });
    }
  
    //   
    if (selectedFilters.includes("")) {
      inf4.forEach((bike) => {
        const { SBIKE_X, SBIKE_Y, SBIKE_SPOT_NM, SBIKE_SHARED, SBIKE_PARKING_CNT } = bike;
        filteredMarkers.push({
          lat: parseFloat(SBIKE_Y),
          lng: parseFloat(SBIKE_X),
          type: "",
          detail: {
            name: SBIKE_SPOT_NM,
            shared: SBIKE_SHARED,
            parkingCnt: SBIKE_PARKING_CNT
          }
        });
      });
    }
  
    return filteredMarkers;
  };
  useEffect(() => {
    const place = touristSpots.find(spot => spot.name === placeName);
    console.log(place)

    if (place) {
      setCurrentPosition({
        lat: place.latitude,
        lng: place.longitude
        
      });
      console.log(currentPosition)

    }
  }, [placeName]);  // placeName   
  
  useEffect(() => {
    // inf1, inf2, inf3, inf4    selectedFilters   
    if (inf1 && inf2 && inf3 && inf4 && selectedFilters.length > 0) {
      const filteredMarkers = integrateData(inf1, inf2, inf3, inf4, selectedFilters);
      setMarkers(filteredMarkers);
      setIsDataLoaded(true); //   
    }
  }, [inf1, inf2, inf3, inf4, selectedFilters]); //   selectedFilters 


  const handleMarkerClick = (marker) => {
    setSelectedMarker(marker);
  };

  return (
    <div className='parent-container'>
      <div className="box_map">
              {/*   API    LoadScript   */}
        <GoogleMap
          mapContainerStyle={containerStyle}
          center={currentPosition}
          zoom={13}
        >
          {/*    */}
          {isPlaceInTouristSpots && (
            <MarkerF position={currentPosition} title=" " />
          )}

          {/*    */}
          {markers.map((marker, index) => (
            <MarkerF
              key={index}
              position={{ lat: marker.lat, lng: marker.lng }}
              icon={markerIcons[marker.type] || "http://maps.google.com/mapfiles/ms/icons/yellow-dot.png"}
              title={marker.detail.name}
              onClick={() => handleMarkerClick(marker)}
            />
          ))}
        </GoogleMap>
      </div>
      {/*       */}
      {selectedMarker && (
        <div className = "box">
          <div className='h2'> -   -</div>
          <h3>{selectedMarker.detail.name}</h3>
          <p>
            <span className='subtitle'>  : </span>
            {selectedMarker.type}
          </p>
          {/*    */}
          {selectedMarker.type === "" && (
            <>
              <img className = "thumbnail" src={selectedMarker.detail.thumbnail} alt="Thumbnail" />
              <p><span className='subtitle'>:</span> {selectedMarker.detail.place}</p>
              <p><span className='subtitle'>:</span> {selectedMarker.detail.period}</p>
              <p><a href={selectedMarker.detail.url} target="_blank" rel="noopener noreferrer"> </a></p>
            </>
          )}
          {selectedMarker.type === "" && (
            <>
              <p><span className='subtitle'>:</span> {selectedMarker.detail.address}</p>
              <p><span className='subtitle'>:</span> {selectedMarker.detail.rates}</p>
              <p><span className='subtitle'>:</span> {selectedMarker.detail.pay}</p>
            </>
          )}
          {selectedMarker.type === "" && (
            <>
              <p><span className='subtitle'>:</span> {selectedMarker.detail.line}</p>
              <p><span className='subtitle'>:</span> {selectedMarker.detail.address}</p>
              <p><span className='subtitle'>:</span> {selectedMarker.detail.jibun}</p>
            </>
          )}
          {selectedMarker.type === "" && (
            <>
              <p><span className='subtitle'>:</span> {selectedMarker.detail.shared}</p>
              <p><span className='subtitle'>  :</span> {selectedMarker.detail.parkingCnt}</p>
            </>
          )}
        </div>
      )}
    </div>
  );
};

export default Map;
